@import url('https://fonts.googleapis.com/css?family=Quicksand');

* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-family: 'Quicksand', sans-serif;
}

.App {
  text-align: center;
  padding: 0;
	margin: 0;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-riippumatto {
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.App-riippumatto-pieni {
	width:100%;
	-o-object-fit: cover;
	   object-fit: cover;
	display: none;
}

.App-header {
  max-width: 100%;
  height: 100px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center; /* align vertical */
	padding-left: 20px;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.App-header p {
  float: left;
	font-size: 2em;
	font-family: 'Quicksand', sans-serif;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.App-navbar-large {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	float: right;
	margin-right: 10px;
}

.App-navbar-small {
	display: none;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	margin-top: 30px;
}

.App-link {
	margin: 10px;
	padding: 10px;
	outline: solid black 1px;
	width: 100px;
	color: black;
	font-weight: bold;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-family: 'Quicksand', sans-serif;
}

a {
	text-decoration: none;	
}

.App-gameimage {
	width: 20vw;
	height: 20vw;
}

.App-gameproject {
	padding: 10px;
	max-width: 100%;
}

.App-videoSmall {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	padding-top: 25px;
	height: 0;
}

.App-videoSmall iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.App-githublogo {
	margin-top: 5px;
	width: 15%;
	border-radius: 25px;
}

.App-githublogo:hover {
	background-color: lightgray;
}

.App-titleSection {
	padding-top: 60px;
	padding-bottom: 30px;
}

.App-textSection {
	padding-left: 15vw;
	padding-right: 15vw;
	text-align: justify;
	-ms-flex-align: center;
	    align-items: center;
}

.App-gameprojects {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
	grid-gap: 20px;
	margin: 20px;
	margin-top: 10px;
}

.App-link:hover {
	background-color: black;
	color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen and (max-width: 650px) {
	.App-gameprojects {
		grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
	}

	.App-link {
		width: 100vw;
		margin: 2px;
		outline: none;
		background-color: lightgray;
	}

	.App-textSection {
		padding-left: 20px;
		padding-right: 20px;
	}

	.App-navbar-large {
		display: none;
	}

	.App-navbar-small {
		display: -ms-flexbox;
		display: flex;	
	}

	.App-header {
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: center;
		    justify-content: center;
	}
	
	.App-header p {
		float: none;
	}

	.App-riippumatto {
		display: none;
	}

	.App-riippumatto-pieni {
		display: block;
	}
}


